.card-img-top{
  height: 280px;
  width:360px;
  border-radius: 5.5%;
}
.card{
  border-radius: 5.5%;
  background-color: white;
  margin:0.5%;
  padding: 1%;
}
/* hover keyword is used for after effect if we hover over that part where the keyword is applied */
.card:hover{
  border-radius: 2.5%;
  transform: scale(1.05);
  box-shadow: 1px 0 10px rgba(0, 0, 0, 0.5);
  background-color: rgb(37, 35, 35);
}

/* with flex direction we are specifying the direction of flex....i,e if it is row then our axis will be row wise that is horizontal */


/* flex-wrap  defines whether we have to wrap our content or not means if it is no wrap then result will be in one single line.....i.e we have no next line ....all of them will be in single line but if it is wrap then content occupies that much space as available on device.....and after occupying that much it simply goes down and next things are displayed in next lines*/

/* display inline flex is used to display in one line and due to flex-wrap=wrap....after completing one line content is moved to next line......if it is block-flex then content will be in vertical fashion means one block in one line....next block in other and so on */

/* gap is used to define the gap between each block */
.container{
  border-radius: 200%;
  margin:0.6%;
  flex-direction: row;
  flex-wrap:wrap;
  display: inline-flex;
  gap:1rem;
}
